import React from 'react';
import { connect } from 'react-redux';
import { Layout, Menu, Icon, Button, Row, Col, Tooltip } from 'antd';
import { Route } from 'react-router-dom';
import { Links } from '../utils/Links';
import UsersContainer from './Users';
import VehiclesContainer from './Vehicles';
import ServicesContainer from './Services';
import { IWithUser, IWithCommittee } from '../reducers/reducers';
import { RouterProps } from 'react-router';
import { PORTAL_NAME } from '../utils/Constants';
import { IResetAuthDataProps, resetAuthData } from '../actions/action';
import PersonalAreaContainer from './PersonalArea';

const { Header, Content, Footer, Sider } = Layout;

interface IDashboardProps extends IWithUser, IWithCommittee, IResetAuthDataProps, RouterProps {}

interface IDashboardState {
  collapsed: boolean;
}

class Dashboard extends React.Component<IDashboardProps, IDashboardState> {
  constructor(props: IDashboardProps) {
    super(props);

    const { user, committee } = this.props;
    if (!user && !committee) {
      this._onLogout();
    }
  }
  state = {
    collapsed: true
  };

  _onCollapse = (collapsed: any) => {
    this.setState({ collapsed });
  };

  _onClickLogout = () => {
    this.props.resetAuthData();
    this._onLogout();
  };

  _onLogout = () => {
    this.props.history.replace(Links.LOGOUT);
  };

  get isUserAdmin(): boolean {
    const { user } = this.props;
    if (user !== undefined && user.committeeUser !== undefined) {
      return user?.committeeUser.role < 2;
    }
    return false;
  }

  public render() {
    const { user, committee } = this.props;
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Sider collapsible collapsed={this.state.collapsed} onCollapse={this._onCollapse}>
          <div style={{ height: '60px', width: '100%' }} />
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <Menu.Item
              key="1"
              onClick={() => {
                this.props.history.push(Links.SERVICES);
              }}
            >
              <Icon type="calendar" />
              <span>Gestione Servizi</span>
            </Menu.Item>
            {this.isUserAdmin ? (
              <Menu.Item
                key="2"
                onClick={() => {
                  this.props.history.push(Links.USERS);
                }}
              >
                <Icon type="team" />
                <span>Gestione Utenti</span>
              </Menu.Item>
            ) : undefined}
            {this.isUserAdmin ? (
              <Menu.Item
                key="3"
                onClick={() => {
                  this.props.history.push(Links.VEHICLES);
                }}
              >
                <Icon type="car" />
                <span>Gestione Veicoli</span>
              </Menu.Item>
            ) : undefined}
          </Menu>
        </Sider>
        <Layout>
          <Header style={{ background: '#001529', maxHeight: '64px', padding: 0 }}>
            <Row>
              <Col span={16} style={{ textAlign: 'left' }}>
                <span
                  style={{
                    color: 'white',
                    marginLeft: '5px',
                    fontStyle: 'bold',
                    fontSize: 'larger'
                  }}
                >
                  {PORTAL_NAME}
                </span>
                -
                <span
                  style={{
                    color: 'white',
                    marginLeft: '5px',
                    fontStyle: 'italic'
                  }}
                >
                  {committee?.description}
                </span>
              </Col>
              <Col span={6} style={{ textAlign: 'right' }}>
                <span style={{ color: 'white', marginRight: '5px' }}>
                  {user?.name} {user?.surname}
                </span>
              </Col>
              <Col span={1}>
                <Tooltip title="Area personale" placement="bottom">
                  <Button
                    ghost
                    icon="user"
                    onClick={() => {
                      this.props.history.push(Links.PERSONAL_AREA);
                    }}
                    style={{ height: '64px', width: '100%' }}
                  />
                </Tooltip>
              </Col>
              <Col span={1}>
                <Tooltip title="Logout" placement="bottom">
                  <Button ghost icon="logout" style={{ height: '64px', width: '100%' }} onClick={this._onClickLogout} />
                </Tooltip>
              </Col>
            </Row>
          </Header>
          <Content style={{ margin: '0 16px' }}>
            <Route path={Links.SERVICES} component={ServicesContainer} />
            <Route path={Links.USERS} component={UsersContainer} />
            <Route path={Links.VEHICLES} component={VehiclesContainer} />
            <Route path={Links.PERSONAL_AREA} component={PersonalAreaContainer} />
          </Content>
          <Footer style={{ textAlign: 'center' }} />
        </Layout>
      </Layout>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    committee: state.appData.committee
  };
};
const DashboardContainer = connect(mapStateToProps, { resetAuthData })(Dashboard);
export default DashboardContainer;
