import * as React from 'react';
import { ServiceRoleType } from '../../dto/TripUsersDTO';
import { Button, Radio, Row, Select } from 'antd';
import { RadioChangeEvent } from 'antd/lib/radio';
import UserDTO from '../../dto/UserDTO';

const { Option } = Select;

interface RoleChooseProps {
  users: UserDTO[];
  isUserAdmin: boolean;
  loggedUser: UserDTO;
  onConfirmRole: (role: number, userId: number) => void;
}

interface RoleChooseState {
  roleSelected: number;
  userSelected: UserDTO;
}

class RoleChoose extends React.Component<RoleChooseProps, RoleChooseState> {
  state = {
    userSelected: this.props.isUserAdmin ? this.props.users[0] : this.props.loggedUser,
    roleSelected: 1
  };

  private _onChangeRole = (e: RadioChangeEvent) => {
    this.setState({ roleSelected: e.target.value });
  };

  private _onChangeUser = (value: number) => {
    const { users } = this.props;
    const userSelected: UserDTO | undefined = users.find((user: UserDTO) => user.id === value);
    if (userSelected !== undefined) {
      this.setState({ userSelected, roleSelected: 1 });
    }
  };

  public render() {
    const { users, isUserAdmin, loggedUser } = this.props;
    const { userSelected, roleSelected } = this.state;
    return (
      <div>
        <Row>
          <Select style={{ width: '100%' }} defaultValue={this.state.userSelected.id} onChange={this._onChangeUser}>
            {users
              .filter((user: UserDTO) => isUserAdmin || user.id === loggedUser.id)
              .map((user: UserDTO) => (
                <Option value={user.id} key={user.id}>
                  {user.name} {user.surname}
                </Option>
              ))}
          </Select>
        </Row>
        <Row>
          <Radio.Group style={{ marginTop: '5px', width: '100%' }} value={roleSelected} buttonStyle="solid" onChange={this._onChangeRole}>
            {userSelected.withDrivingLicense === 1 ? <Radio.Button value={0}>{ServiceRoleType[0]}</Radio.Button> : undefined}
            <Radio.Button value={1}>{ServiceRoleType[1]}</Radio.Button>
            {userSelected.committeeUser.occupation === 2 ? <Radio.Button value={2}>{ServiceRoleType[2]}</Radio.Button> : undefined}
          </Radio.Group>
        </Row>
        <Row>
          <Button style={{ marginTop: '5px', width: '100%' }} onClick={() => this.props.onConfirmRole(this.state.roleSelected, this.state.userSelected.id)}>
            Conferma
          </Button>
        </Row>
      </div>
    );
  }
}

export default RoleChoose;
